/* ==========================================
   CHECKOUT METHODS (PAYMENT) SECTION
========================================== */

.checkout-methods {
    background: #FFFFFF;
    border-radius: 12px;
    padding: 24px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.06);
    transition: all 0.3s ease;
}

.checkout-methods:hover {
    box-shadow: 0 4px 16px rgba(0, 0, 0, 0.1);
}

/* ==========================================
   HEADING
========================================== */

.checkout-methods h3 {
    font-size: 20px;
    font-weight: 600;
    color: #333333;
    margin: 0 0 20px 0;
    padding-bottom: 12px;
    border-bottom: 2px solid #F5E1DA;
    display: flex;
    align-items: center;
    gap: 8px;
}

.checkout-methods h3::before {
    content: "";
    font-size: 24px;
}

/* ==========================================
   METHOD ITEM (RADIO OPTION)
========================================== */

.checkout-methods .method-item {
    display: flex;
    align-items: center;
    padding: 16px;
    border: 2px solid #e0e0e0;
    border-radius: 8px;
    margin-bottom: 12px;
    cursor: pointer;
    transition: all 0.3s ease;
    position: relative;
    background: #FFFFFF;
    font-size: 15px;
    color: #333333;
    font-weight: 500;
}

.checkout-methods .method-item:last-child {
    margin-bottom: 0;
}

.checkout-methods .method-item:hover {
    border-color: #E67E22;
    background: #FFF5ED;
    transform: translateX(4px);
}

/* Active/Selected state */
.checkout-methods .method-item.active {
    border-color: #E67E22;
    background: linear-gradient(135deg, #FFF5ED 0%, #FDEEE6 100%);
    box-shadow: 0 2px 8px rgba(230, 126, 34, 0.15);
    font-weight: 600;
}

.checkout-methods .method-item.active::before {
    content: "";
    position: absolute;
    top: 8px;
    right: 8px;
    width: 24px;
    height: 24px;
    background: #E67E22;
    color: white;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 14px;
    font-weight: bold;
}

/* ==========================================
   RADIO INPUT
========================================== */

.checkout-methods .method-item input[type="radio"] {
    width: 20px;
    height: 20px;
    margin-right: 12px;
    cursor: pointer;
    accent-color: #E67E22;
    flex-shrink: 0;
}

/* ==========================================
   PAYMENT ICONS (EMOJI)
========================================== */

.checkout-methods .method-item::after {
    content: "";
    margin-left: auto;
    font-size: 24px;
    transition: transform 0.2s ease;
}

/* COD specific icon enhancement */
.checkout-methods .method-item:has(input[value="COD"])::after,
.checkout-methods .method-item:nth-child(2)::after {
    content: "";
}

/* Banking specific icon enhancement */
.checkout-methods .method-item:has(input[value="BANKING"])::after,
.checkout-methods .method-item:nth-child(3)::after {
    content: "";
}

.checkout-methods .method-item:hover::after {
    transform: scale(1.2);
}

/* ==========================================
   RECOMMENDED BADGE (OPTIONAL)
========================================== */

.checkout-methods .method-item .recommended-badge {
    position: absolute;
    top: -8px;
    left: 50%;
    transform: translateX(-50%);
    background: linear-gradient(135deg, #E67E22 0%, #F39C12 100%);
    color: white;
    padding: 4px 12px;
    border-radius: 12px;
    font-size: 11px;
    font-weight: 700;
    text-transform: uppercase;
    letter-spacing: 0.5px;
    box-shadow: 0 2px 6px rgba(230, 126, 34, 0.3);
    animation: pulse 2s infinite;
}

@keyframes pulse {
    0%, 100% {
        opacity: 1;
        transform: translateX(-50%) scale(1);
    }
    50% {
        opacity: 0.9;
        transform: translateX(-50%) scale(1.05);
    }
}

/* ==========================================
   PAYMENT INFO TEXT (OPTIONAL)
========================================== */

.checkout-methods .payment-info {
    margin-top: 16px;
    padding: 12px 16px;
    background: linear-gradient(135deg, #F5E1DA 0%, #FDEEE6 100%);
    border-left: 4px solid #E67E22;
    border-radius: 6px;
    font-size: 13px;
    color: #666;
    line-height: 1.6;
}

.checkout-methods .payment-info strong {
    color: #333333;
    font-weight: 600;
}

/* ==========================================
   RESPONSIVE DESIGN
========================================== */

@media (max-width: 768px) {
    .checkout-methods {
        padding: 20px 16px;
    }

    .checkout-methods h3 {
        font-size: 18px;
    }

    .checkout-methods .method-item {
        padding: 14px;
        font-size: 14px;
    }

    .checkout-methods .method-item.active::before {
        width: 20px;
        height: 20px;
        font-size: 12px;
        top: 6px;
        right: 6px;
    }

    .checkout-methods .method-item::after {
        font-size: 20px;
    }
}

@media (max-width: 480px) {
    .checkout-methods {
        padding: 16px 12px;
    }

    .checkout-methods h3 {
        font-size: 16px;
    }

    .checkout-methods .method-item {
        padding: 12px;
        font-size: 13px;
        flex-wrap: wrap;
    }

    .checkout-methods .method-item input[type="radio"] {
        margin-right: 8px;
    }

    .checkout-methods .method-item::after {
        position: absolute;
        right: 40px;
        font-size: 18px;
    }
}

/* ==========================================
   ANIMATION
========================================== */

@keyframes fadeIn {
    from {
        opacity: 0;
        transform: translateY(10px);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}

.checkout-methods {
    animation: fadeIn 0.4s ease;
}

/* Stagger animation for method items */
.checkout-methods .method-item:nth-child(2) {
    animation: fadeIn 0.4s ease 0.1s backwards;
}

.checkout-methods .method-item:nth-child(3) {
    animation: fadeIn 0.4s ease 0.2s backwards;
}

/* ==========================================
   DISABLED STATE (OPTIONAL)
========================================== */

.checkout-methods .method-item.disabled {
    opacity: 0.5;
    cursor: not-allowed;
    background: #f5f5f5;
}

.checkout-methods .method-item.disabled:hover {
    border-color: #e0e0e0;
    background: #f5f5f5;
    transform: none;
}

.checkout-methods .method-item.disabled input[type="radio"] {
    cursor: not-allowed;
}

/* ==========================================
   LOADING STATE (OPTIONAL)
========================================== */

.checkout-methods.loading {
    opacity: 0.6;
    pointer-events: none;
}

.checkout-methods.loading::after {
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 30px;
    height: 30px;
    border: 3px solid #F5E1DA;
    border-top-color: #E67E22;
    border-radius: 50%;
    animation: spin 0.8s linear infinite;
}

@keyframes spin {
    from {
        transform: translate(-50%, -50%) rotate(0deg);
    }
    to {
        transform: translate(-50%, -50%) rotate(360deg);
    }
}

/* ==========================================
   ACCESSIBILITY
========================================== */

.checkout-methods .method-item:focus-within {
    outline: 2px solid #E67E22;
    outline-offset: 2px;
}

.checkout-methods .method-item input[type="radio"]:focus {
    outline: none;
}

/* Screen reader only text */
.sr-only {
    position: absolute;
    width: 1px;
    height: 1px;
    padding: 0;
    margin: -1px;
    overflow: hidden;
    clip: rect(0, 0, 0, 0);
    white-space: nowrap;
    border-width: 0;
}

/* ==========================================
   SPECIAL EFFECTS
========================================== */

/* Glow effect for active payment method */
.checkout-methods .method-item.active {
    animation: glow 2s ease-in-out infinite;
}

@keyframes glow {
    0%, 100% {
        box-shadow: 0 2px 8px rgba(230, 126, 34, 0.15);
    }
    50% {
        box-shadow: 0 4px 16px rgba(230, 126, 34, 0.25);
    }
}

/* Hover effect for emoji icons */
.checkout-methods .method-item:hover {
    /* Icon emoji s scale ln hn nh ::after */
}

/* Active state text color enhancement */
.checkout-methods .method-item.active {
    color: #333333;
}