/* ==========================================
   SHIPPING METHODS SECTION
========================================== */

.shipping-methods-section {
    background: #FFFFFF;
    border-radius: 12px;
    padding: 24px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.06);
    transition: all 0.3s ease;
}

.shipping-methods-section:hover {
    box-shadow: 0 4px 16px rgba(0, 0, 0, 0.1);
}

/* ==========================================
   HEADING
========================================== */

.shipping-methods-section h3 {
    font-size: 20px;
    font-weight: 600;
    color: #333333;
    margin: 0 0 20px 0;
    padding-bottom: 12px;
    border-bottom: 2px solid #F5E1DA;
    display: flex;
    align-items: center;
    gap: 8px;
}

.shipping-methods-section h3::before {
    content: "";
    font-size: 24px;
}

/* ==========================================
   METHODS LIST
========================================== */

.methods-list {
    display: flex;
    flex-direction: column;
    gap: 12px;
}

/* ==========================================
   METHOD ITEM (RADIO OPTION)
========================================== */

.method-item {
    display: flex;
    align-items: center;
    padding: 16px;
    border: 2px solid #e0e0e0;
    border-radius: 8px;
    cursor: pointer;
    transition: all 0.3s ease;
    position: relative;
    background: #FFFFFF;
}

.method-item:hover {
    border-color: #E67E22;
    background: #FFF5ED;
    transform: translateX(4px);
}

/* Active/Selected state */
.method-item.active {
    border-color: #E67E22;
    background: linear-gradient(135deg, #FFF5ED 0%, #FDEEE6 100%);
    box-shadow: 0 2px 8px rgba(230, 126, 34, 0.15);
}

.method-item.active::before {
    content: "";
    position: absolute;
    top: 8px;
    right: 8px;
    width: 24px;
    height: 24px;
    background: #E67E22;
    color: white;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 14px;
    font-weight: bold;
}

/* ==========================================
   RADIO INPUT
========================================== */

.method-item input[type="radio"] {
    width: 20px;
    height: 20px;
    margin-right: 12px;
    cursor: pointer;
    accent-color: #E67E22;
    flex-shrink: 0;
}

/* ==========================================
   METHOD INFO
========================================== */

.method-info {
    flex: 1;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 16px;
}

.method-info span {
    font-size: 15px;
    color: #333333;
    font-weight: 500;
    display: flex;
    align-items: center;
    gap: 8px;
}

.method-info strong {
    font-size: 16px;
    font-weight: 700;
    color: #E67E22;
    flex-shrink: 0;
}

/* Active state text */
.method-item.active .method-info span {
    color: #333333;
    font-weight: 600;
}

.method-item.active .method-info strong {
    color: #A93226;
}

/* ==========================================
   FREE SHIPPING BADGE (OPTIONAL)
========================================== */

.method-item .free-badge {
    position: absolute;
    top: -8px;
    right: 60px;
    background: linear-gradient(135deg, #52c41a 0%, #73d13d 100%);
    color: white;
    padding: 4px 12px;
    border-radius: 12px;
    font-size: 11px;
    font-weight: 700;
    text-transform: uppercase;
    letter-spacing: 0.5px;
    box-shadow: 0 2px 6px rgba(82, 196, 26, 0.3);
    animation: pulse 2s infinite;
}

@keyframes pulse {
    0%, 100% {
        opacity: 1;
        transform: scale(1);
    }
    50% {
        opacity: 0.9;
        transform: scale(1.05);
    }
}

/* ==========================================
   RESPONSIVE DESIGN
========================================== */

@media (max-width: 768px) {
    .shipping-methods-section {
        padding: 20px 16px;
    }

    .shipping-methods-section h3 {
        font-size: 18px;
    }

    .method-item {
        padding: 14px;
    }

    .method-info {
        flex-direction: column;
        align-items: flex-start;
        gap: 8px;
    }

    .method-info span {
        font-size: 14px;
    }

    .method-info strong {
        font-size: 15px;
        align-self: flex-end;
    }

    .method-item.active::before {
        width: 20px;
        height: 20px;
        font-size: 12px;
        top: 6px;
        right: 6px;
    }
}

@media (max-width: 480px) {
    .shipping-methods-section {
        padding: 16px 12px;
    }

    .shipping-methods-section h3 {
        font-size: 16px;
    }

    .method-item {
        padding: 12px;
        flex-direction: column;
        align-items: flex-start;
        gap: 12px;
    }

    .method-item input[type="radio"] {
        margin-right: 0;
        margin-bottom: 8px;
    }

    .method-info {
        width: 100%;
        padding-left: 0;
    }
}

/* ==========================================
   ANIMATION
========================================== */

@keyframes fadeIn {
    from {
        opacity: 0;
        transform: translateY(10px);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}

.shipping-methods-section {
    animation: fadeIn 0.4s ease;
}

/* Stagger animation for method items */
.method-item:nth-child(1) {
    animation: fadeIn 0.4s ease 0.1s backwards;
}

.method-item:nth-child(2) {
    animation: fadeIn 0.4s ease 0.2s backwards;
}

.method-item:nth-child(3) {
    animation: fadeIn 0.4s ease 0.3s backwards;
}

/* ==========================================
   DISABLED STATE (OPTIONAL)
========================================== */

.method-item.disabled {
    opacity: 0.5;
    cursor: not-allowed;
    background: #f5f5f5;
}

.method-item.disabled:hover {
    border-color: #e0e0e0;
    background: #f5f5f5;
    transform: none;
}

.method-item.disabled input[type="radio"] {
    cursor: not-allowed;
}

/* ==========================================
   LOADING STATE (OPTIONAL)
========================================== */

.shipping-methods-section.loading {
    opacity: 0.6;
    pointer-events: none;
}

.shipping-methods-section.loading::after {
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 30px;
    height: 30px;
    border: 3px solid #F5E1DA;
    border-top-color: #E67E22;
    border-radius: 50%;
    animation: spin 0.8s linear infinite;
}

@keyframes spin {
    from {
        transform: translate(-50%, -50%) rotate(0deg);
    }
    to {
        transform: translate(-50%, -50%) rotate(360deg);
    }
}

/* ==========================================
   DELIVERY TIME INDICATOR
========================================== */

.method-info .delivery-time {
    font-size: 13px;
    color: #666;
    font-weight: 400;
    margin-left: 4px;
}

.method-item.active .method-info .delivery-time {
    color: #E67E22;
}

/* ==========================================
   ICON STYLING
========================================== */

.method-info span::before {
    font-size: 20px;
}

/* Make emoji icons slightly bigger and more visible */
.method-item:hover .method-info span::before {
    transform: scale(1.2);
    transition: transform 0.2s ease;
}

/* ==========================================
   ACCESSIBILITY
========================================== */

.method-item:focus-within {
    outline: 2px solid #E67E22;
    outline-offset: 2px;
}

.method-item input[type="radio"]:focus {
    outline: none;
}

/* Screen reader only text */
.sr-only {
    position: absolute;
    width: 1px;
    height: 1px;
    padding: 0;
    margin: -1px;
    overflow: hidden;
    clip: rect(0, 0, 0, 0);
    white-space: nowrap;
    border-width: 0;
}